body{
  font-family: sans-serif;
  background-color: rgb(4, 4, 77);
}

.main{
  background-color: white;
  margin: auto;
  height: 70vh;
  margin-top: 3rem;
  max-width: 400px;
  border-radius: 10px;
}

.main{
  display: flex;
  gap: 10px;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.die-container{
  display: grid;
  grid-template: auto auto/ repeat(5,1fr);
  gap: 20px;
}

.die-face{
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0,0,0,0.15);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-weight: 700;
  font-size: 20px;
}

.roll-btn{
  width: 108px;
  padding: 9px 19px;
  margin: 15px auto;
  background-color: rgb(8, 8, 131);
  color: white;
  border: 0px;
  border-radius: 10px;
}
.roll-btn:hover{
  background-color: rgb(5, 5, 73);

}
.roll-btn:active{
  background-color: rgb(9, 9, 204);

}

.green{
  background-color: #59E391;
}

h1,h4{
  text-align: center;
}
.desc{
  font-weight: 600;
  font-size: 16px;
  max-width: 320px;
  align-items: center;
  text-align: center;
}